    </el-tooltip>
    <el-tooltip class="item" effect="dark" content="Bottom Right 提示文字" placement="bottom-end">
      <el-button>下右</el-button>
    </el-tooltip>
  </div>
</div></div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
    <el-col :span="6">
<div class="grid-content"><el-tooltip content="Top center" placement="top">
  <el-button>Dark</el-button>
</el-tooltip></div>
    </el-col>
    <el-col :span="6">
<div class="grid-content"><el-tooltip content="Bottom center" placement="bottom" effect="light">
  <el-button>Light</el-button>
</el-tooltip></div>
    </el-col>
    <el-col :span="6">
<div class="grid-content"><el-tooltip placement="top">
  <div slot="content">多行信息<br/>第二行信息</div>
  <el-button>Top center</el-button>
</el-tooltip></div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex'
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
